@import '@dpe/base/src/global';
@import 'bootstrap/scss/forms';

// Forms
//
// Package: `@dpe/form`
//
// See [Bootstrap forms](https://getbootstrap.com/docs/4.1/components/forms/) for usage details.
// Some styles have been overridden as demonstrated below.
//
// Style guide: 4.forms

.form-text.text-muted,
.description {
  font-size: $text-xs;
  margin: $spacer-s 0;

  // sass-lint:disable no-important
  color: $black !important;
}

// Controls
//
// Markup: controls.twig
//
// Style guide: 4.forms.controls

select.form-control {
  @include icon('chevron-down', 20px);
  background-position: right 12px center;
  padding-right: $spacer-l;
  cursor: pointer;

  // sass-lint:disable no-vendor-prefixes
  appearance: none;

  // Hide default arrow.
  &::-ms-expand {
    display: none;
  }

  &[multiple] {
    background-image: none;
  }
}

// Icon integration
//
// Extend the Bootstrap form controls with our icon library. Simply add the
// `.form-control-icon` class along with any [icon](/components-library/dist/styleguide/section-3.html#kssref-3-default)
// class from the available set.
//
// **Make sure you have the `@dpe/icons` package installed and are including it's styles.**
//
// Markup: icon-controls.twig
//
// Style guide: 4.forms.icon

.form-control-icon {
  padding-left: $spacer-xl;
  background-position: left $spacer-m center;
}

// Checkbox and Radios
//
// Extend the Bootstrap radios and checkbox styling with the `.form-check-fancy` class.
//
// Markup: checkbox-radio.twig
//
// Style guide: 4.forms.checkbox-radio

$check-size:          ($spacer * 1.375);
$check-check-size:    ($spacer * .875);

.form-check-fancy {
  margin: 0 0 $spacer-xs;
  padding-left: 0;

  &.form-group {
    margin-bottom: $form-group-margin-bottom;
  }

  label {
    cursor: pointer;
    line-height: 1.5;

    // pseudo checkbox/radio
    &::before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      margin-right: $spacer-s;
      width: $check-size;
      height: $check-size;
      border: 2px solid $input-border-color;
      border-radius: $border-radius;
    }
  }

  [type='radio'],
  [type='checkbox'] {
    @include sr-only;
    margin: 0;

    &:checked + label::after {
      content: '';
      display: block;
      position: absolute;
      width: $check-check-size;
      height: $check-check-size;
      top: $spacer-xs + ($spacer * .1875);
      left: $spacer-xs;
    }

    &:disabled + label::after,
    &:disabled + label::before {
      opacity: .25;
    }
  }

  [type='checkbox'] {
    &:checked + label::after {
      @include icon('check');
    }
  }

  [type='radio'] {
    + label::before {
      border-radius: 50%;
    }

    &:checked + label::after {
      background-color: $primary;
      border-radius: 50%;

      // IE only fix for radio checkmark.
      @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        top: $spacer-s;
      }
    }
  }
}

// Label
//
// Markup: label.twig
//
// Style guide: 4.forms.label

label,
legend {
  font-size: $text-s;
  line-height: 1;
}

// Stacked form
//
// Markup: form.twig
//
// Style guide: 4.forms.form-stacked

// Inline form
//
// Markup: inline-form.twig
//
// Style guide: 4.forms.form-inline

.form-inline {
  label {
    @include sr-only;
  }

  .form-group {
    margin: 0 $spacer-s $spacer-s 0;
  }

  .btn {
    margin-bottom: $spacer-s;
  }
}

.form-group > .row {
  align-items: center;
}

